@import "../../sass";

.container {
    padding-top: 100px;
    background-color: $_br_viewsPage;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    .options_bar {
        @include d-f(center, center);
        flex-wrap: wrap;
        @include smallest {
            max-width: 500px;
            margin: 0 10px;  
            padding: 10px;
            background: linear-gradient(10deg, #1e3799, #60a3bc, #38ada9);
        }
        .selector_obtion {
            @include d-f(center, center);
            padding: 4px 8px;
            @include smallest {
                width: 100%;
                margin: 5px 0;
            }
            & label {
                color: #d1d1d1;
                font-family: 'Montserrat',sans-serif;
            }
            .select_list_moto {
                margin-left: 10px;
                & option {
                    font-family: 'Montserrat',sans-serif;
                }
            }
        }
        input {
            @include smallest {
                width: 100%;
                margin: 5px 0;
            }
            padding: 4px 8px;
            font-size: 15px;
            border: none;
            outline: none;
            transition: .3s linear;
            &:focus {
                border: 1px solid red;
                background-color: $_000;
                color: $_fff;
            }
        }
        .options_Sort {
            
            h4 {
                cursor: pointer;
            }
        }
    }
    .list_item {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        list-style: none;
        width: 80%;
        padding: 20px;
        @include smallest {
            width: 100%;
            margin: 5px 0;
        }
        // @include landscape_phones {
        //     width: 80%;
        // }
        .item {
            &:hover {
                transform: translateY(-2px);
                box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.6);
            }
            margin: 0 8px;
            transition: cubic-bezier(0.175, 0.885, 0.32, 1.275) .4s;
            margin-bottom: 70px; 
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            position: relative;
            border-radius: 5px;
            overflow: hidden;
            box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.6);
            width: 300px;
            .item_top {
                .link_item_top {
                    overflow: hidden;
                    &:hover .item_img {
                        transform: scale(1.08);
                    }
                    display: flex;
                    .item_img {
                        width: 100%;
                        transition: 1s cubic-bezier(0.175, 0.885, 0.32, 1.275);
                    }
                }
            }
            .item_info {
                display: flex;
                padding-top: 10px;
                background-color: $_fff;
                .item_info_left {
                    width: 60%;
                    display: flex;
                    flex-direction: column;
                    & a {
                        display: block;
                        padding: 8px;
                        color: $_222;
                        text-decoration: none;
                    }
                    .item_cc {
                        font-weight: bold;
                    }
                    .item_name {
                        font-weight: bold;
                        &:hover {
                            transition: all 0.6s ease;
                            text-shadow: 2px 3px 1px rgb(0 0 0 / 41%);
                        }
                        font-size: 1rem;
                        padding-left: 6px;
                        position: relative;
                        &::after {
                            @include absolute(auto, 10%, 2px, 6px);
                            height: 2px;
                            background-color: $_border_primary;
                        }
                    }
                }
                .item_price {
                    padding-left: 8px;
                    margin: 6px 0;
                }
                .item_info_right {
                    width: 40%;
                    display: flex;
                    align-items: start;
                    .moreInfoMoto {
                        margin-left: 4px;
                        cursor: pointer;
                        padding: 10px;
                        background-color: $_000;
                        color: $_fff;
                        font-weight: bold;
                        border-bottom-left-radius: 14px;
                        border-top-right-radius: 14px;
                        border: 1px solid $_border_primary;
                        transition: .3s linear;
                        &:hover {
                            border-bottom-right-radius: 14px;
                            border-top-left-radius: 14px;
                            border-bottom-left-radius: 0;
                            border-top-right-radius: 0;
                            background-color: $_fff;
                            color: $_000;
                        }
                    }
                }
            }
        }
    }
}